.image{
  display: flex;
  position: relative;
  margin-top: -10rem;
  .circle{
      position: absolute;
      z-index: 1;
      top: 22rem;
      right: 6.5rem;
      .cir{
          width: 18rem;
      }
  }
  .proj{
      position: relative;
      z-index: 5;
      top: 22rem;
      left: 25rem;
      .pj{
          width: 21rem;
      }
  }
  .light{
      position: relative;
      z-index: 3;
      right: 21rem;        
      top:0rem;
      .lt{
          width: 32rem;
          height: 28rem;
      }
  }
}

@media screen and (max-width:1230px) and (min-width:940px) {
  .image{
      margin-left: -8rem;
      margin-top: -11rem;
  }
}
@media screen and (max-width:950px) and (min-width:750px) {
  .image{
      margin-left: -10rem;
      margin-top: -15rem;
  }
}